$c-bg : #FEF8ED;
$c-primary: #484AB3;
$c-secondary: #F052EA;

$font-stack-serif : "utopia-std", serif;
$font-stack-label : 'arial', sans-serif;
$font-stack-sans : 'Montserrat', sans-serif;

$spacing-half: 0.685rem;
$spacing-1: 1.35rem;
$spacing-1-and-half: 0.9rem;
$spacing-2: 2.699rem;
$spacing-3: 3.41rem;
$spacing-4: 5.45rem;
$spacing-5: 6.85rem;

$border-thick: 3px solid $c-primary;



